/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-purple: #4b145b;
  --clr-red-special: #b4345c;
  --clr-grey-special: #eae6eb;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 920px;
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: var(--clr-grey-1);
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-5);
}







/*
=============== 
Questions
===============
*/

main {
  min-height: 100vh;
  /* using flex because of better browser support */
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 90vw;
  margin: 5rem auto;
  background: var(--clr-white);
  border-radius: var(--radius);
  padding: 2.5rem 2rem;
  max-width: var(--fixed-width);
  display: flex;
  flex-wrap: wrap-reverse;
  gap: 2rem 2rem;
  box-shadow: var(--dark-shadow);
}
.container h3 {
  line-height: 1.2;
  font-weight: 500;
}
.wrong{
  background: red;
  
}
.default{
  background: var(--clr-purple);
}
.right{
  background: var(--clr-primary-5);
  
}
.menu{
  display: none;
}
.line{
  height: 0.20rem;
    width: 100%;
}
.quiz{
  padding: 1rem 2rem;
  margin-top: 2rem;
  
  
}

.option{
  padding: 16px 32px;
  border: 1px solid var(--clr-purple);
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.option h4{
  margin: 0;
  padding: 0;
}
.info{
  width: 75%;
}
.quiz-text{
  margin-bottom: 2rem;
  border-radius: var(--radius);
  text-align: center;
  padding: 2rem;
  user-select: none;
}
input[type=radio]{
  display: none;
}
.status-div{
  position: relative;
  min-width: 20%;
}
a.learn-button{
margin-top: 4rem !important;

}
table tr{
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 17px;
  padding: 7px 0;
  
}
table{
  width: 100%;
}
.start-button{
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.start-button p{
  margin: 0;
  padding: 0;
  font-size: 30px;
  margin-bottom: 0.75rem;
}
.start-button button{
  background: var(--clr-purple);
  padding: 12px 32px;
  color: white;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.25rem;
  cursor: pointer;
  border-radius: var(--radius);
  box-shadow: var(--light-shadow);
}
.start-button button:hover{
  box-shadow: var(--dark-shadow);
}
.quiz h3{
  text-transform: none;
}
.check-button{
  background: var(--clr-purple);
  color: white;
  border: none;
  padding: 12px 30px;
  float: right;
  cursor: pointer;
}
.info-page .container{
  min-width: 100%;
  max-width: 100%;
  scroll-behavior: smooth;
  margin: 0;
  display: grid;
  


}
.info-page .container div{
  max-width: 100%;
}
.info-page .container p{
  margin-top: 2rem;

}
.side{
  padding-top: 3rem;
  position: fixed;
  width: 20vw;
  height: 100vh;
  top: 0;
  left: 0;
  padding: 5rem 2rem;
}
.info-page .container .side a{
  display: block;
  font-size: 20px;
  padding: 16px 0;
  text-align: center;
  translate: var(--transition);
  cursor: pointer;
}
.side a:hover{
  background: var(--clr-purple);
  color: white;
}
.info-content{
  width: 70vw;
  margin-left: 20vw;
}
img{
  max-width: 100%;
}
.info-content h4{
  font-size: 30px;
}
a.back-button{
  position: absolute;
  bottom: 2rem;
  padding: 8px 0 !important;
  border-radius: var(--radius);
  background: var(--clr-purple);
  color: white;
  width: 90%;
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
/*  global classes */
@media screen and (max-width: 992px) {
  .info{
    min-width: 100%;
  }
  .status-div{
    width: 100%;
  }
  .start-button p{
    font-size: 18px;
    text-align: center;
    margin-bottom: 3rem;
  }
  .side{
    display: none;
    position: fixed;
    top: 0;
    background-color: white;
    width: 100%;
    height: fit-content;
    padding: 2rem;
  }
  .side a{
    margin-top: 0 !important;

  }
  a.back-button{
    position: relative;
    bottom: 0;
    top: 10px;
    width: 100%;
    
  }
  .info-page .container{
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem;
  }
  .menu{
  color: black;
  font-size: 20px;
  }
  .info-content{
    width: 100%;
    margin: 0;
  }
  .menu{
    display: block;
  }
  
}
